import Link from 'next/link';

const Navbar = () => (
    <div className="j-top-nav">
        <div className='j-top-nav-container'>
            <img className="j-logo" src="https://image.shutterstock.com/image-vector/mountain-logo-260nw-505360261.jpg"/>
            <div className="j-content-container">
                <div className="j-flex">
                    <div className="search-container">
                        <input type="text" name="main-search" placeholder="Search for clothing" />
                    </div>
                    <div className="cart-container">
                        <div>Cart</div>
                    </div>
                </div>
                <div className="j-flex">
                    <div className="j-nav-item">Home</div>
                    <div className="j-nav-item">Categories</div>
                    <div className="j-nav-item">Collections</div>
                    <div className="j-nav-item">All Products</div>
                </div>
            </div>
        </div>
        <style jsx>{`
            .j-top-nav-container {
                margin: 10px 5%;
                height: 120px;
            }
            .j-flex {
                display: flex;
                justify-content: space-around;
            }
            .j-nav-item {
                cursor: pointer;
                padding: 10px 20px;
            }
            .j-logo {
                position: absolute;
                display: inline-block;
                width: 10%;
            }
            .j-content-container {
                display: inline-block;
                width: 90%;
            }
        `}</style>
    </div>
)

export default Navbar